.portafolio {
	padding: 5rem 0;
	background: #ececec;
}
@media screen and (max-width: 990px) {
	.portafolio {
		padding: 6rem 0;
	}
} 
.portafolio h1 {
	text-align: center;
	font-size: 3rem;
}
@media screen and (max-width: 990px) {
	.portafolio h1 {
		font-size: 2rem;
	}
}
.tags {
	text-align: center;
	padding: 50px 0px;
	/* background-color: rgb(43, 92, 226); */
}

.tag {
	outline: none;
	border: none;
	color: #000;
	margin: 0px 10px;
	background-color: transparent;
	cursor: pointer;
}

.active {
	font-weight: bold;
	border-bottom: 2px solid #ffcc00;
}
.portaf {
	width: 100%;
    max-width: 1100px;
	margin: auto;
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 10px;
}
@media screen and (max-width: 768px) {
	.portaf {
		grid-template-columns: repeat(2, 1fr);
	}
}

.image-card {
	padding: 5px;
	margin: 5px;
	position: relative;
	/* border: 1px solid #ddd;
	border-radius: 2px; */
	
}

.image {
	width: 100%;
}
.leyenda {
	width: 100%;
	display: block;
	padding-bottom: 1rem;
}
.leyenda a {
	text-decoration: none;
	color: #000;
	font-weight: 500;
}
.leyenda a:hover {
	color:#ffae00;
}
p.describo {
	text-align: center;
	font-size: 15px;
}